@font-face {
  font-family: 'Nebulosa';
  font-weight: 900;
  font-style: normal;
}
html {
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  color: #ffffff;
  background-color: #020413;
  text-align: center;
  text-transform: uppercase;
}

body {
  margin: 0;
  background: url('../../assets/img/bg.jpg') center no-repeat;
  background-size: 100% auto;
  background-attachment: fixed;
}
h2 {
  font-size: 46px;
  margin: 0 0 35px;
  text-align: center;
  color: white;
}

h3 {
  font-size: 30px;
  margin: 0 0 20px;
  color: #ffffff;
  text-align: center;
}

p {
  margin: 0 0 25px;
  font-family: 'Rubik', sans-serif;
}

a {
  color: #910098;
}

.section {
  padding: 30px 0 30px;
  overflow: hidden;
}
.section.bg-hidden {
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.section-top {
  position: relative;
  padding: 160px 0 20px;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
}
.section-top .top-logo {
  z-index: 1;
  position: absolute;
  display: block;
  max-width: 100%;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 1500px) {
  .section-top .top-logo {
    top: 50px;
  }
}
@media (max-width: 1250px) {
  .section-top .top-logo {
    top: 30px;
  }
}
@media (max-width: 1000px) {
  .section-top .top-logo {
    top: 10px;
  }
}
@media (max-width: 960px) {
  .section-top .top-logo {
    display: none;
  }
}
.section-top .top-heading {
  position: absolute;
  display: block;
  max-width: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.section-top .top-heading-mobile {
  position: absolute;
  display: none;
  max-width: 100%;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.container {
  max-width: 640px;
  padding: 0 30px;
  margin: 0 auto;
}

.counter-wrapper {
  width: 380px;
  height: 82px;
  margin: 0 auto 45px;
  box-sizing: border-box;
  border: 5px solid #ffffff;
  background: radial-gradient(#292c72, #040107) !important;
  transform: skew(-20deg);
  background: #292c72;
  line-height: 70px;
  font-size: 30px;
  color: #ffffff !important;
  white-space: nowrap;
  border-radius: 6px;
  text-align: center;
}

.odometer.odometer-auto-theme,
.odometer.odometer-theme-digital {
  background: transparent;
  font-family: 'Wallpoet', monospace;
  padding: 0 0.2em;
  line-height: 70px;
  color: inherit;
  font-size: 32px;
  margin-top: -5px;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu > * {
  margin-right: 25px;
}
.menu > *:last-child {
  margin-right: 0;
}

.button {
  position: relative;
  display: block;
  width: 257px;
  height: auto;
  font-size: 15px;
  padding: 0;
  margin: 0 auto 30px;
  background-color: transparent;
  border: none;
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  line-height: 4;
}
.button:focus {
  outline: none;
}
.button .button-content {
  position: relative;
  margin: 0 10px;
  height: 100%;
  line-height: 96px;
  border-radius: 3px;
  z-index: 999;
  font-family: 'Rubik', sans-serif;
  text-align: center;
  color: white;
}
.button .button-image {
  position: absolute;
  top: 0px;
  left: 6px;
  bottom: 3px;
  right: 9px;
}
.button .button-image img {
  width: 100%;
  height: 100%;
}

.panel-join {
  position: relative;
  padding: 0 125px 0 135px;
  margin-bottom: 30px;
  max-width: 100%;
}
.panel-join-display {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid #fff;
  padding: 10px 15px;
  line-height: 1;
  font-size: 26px;
  margin-bottom: 30px;
  color: white;
}

.panel-join-values {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 18px;
  color: white;
}
.panel-join-values a {
  white-space: nowrap;
  width: 32%;
  position: relative;
  display: block;
  color: #ffffff;
  text-decoration: none;
  padding: 15px 15px;
  margin-bottom: 30px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  overflow: visible;
  font-size: 15px;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  border: 1px dashed #fff;
}
.panel-join-values a::before {
  position: absolute;
  display: block;
  top: 2px;
  left: 3px;
  width: calc(100% - 6px);
  height: calc(100% - 4px);
  content: '';
  background-color: #0f0b27;
  z-index: -1;
  transition: all 0.5s;
}
/* .panel-join-values a:hover::before,
.panel-join-values a.active::before {
} */

.panel-join-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.panel-join-controls .button {
  width: 222px;
  height: 76px;
  font-size: 17px;
  margin: 0 0 19px;
  font-weight: 700;
  text-shadow: 1px 1px 1px #000;
  text-transform: inherit;
}
.panel-join-controls .button .button-content {
  line-height: 77px;
  margin: 0 10px;
}
/* .panel-join-controls .button:last-child .button-content {
} */
.panel-join-controls .button:last-child .button-content::before {
  position: absolute;
  left: -14px;
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 26px 14px 25px 0;
  border-color: transparent #ff0000 transparent transparent;
}
.panel-join-controls .button:last-child .button-content::after {
  position: absolute;
  right: -14px;
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 26px 0 25px 14px;
  border-color: transparent transparent transparent #ff0000;
}
.panel-join-controls .button .button-image {
  top: 3px;
  left: 2px;
  right: 2px;
  bottom: 3px;
}
.container h2.join {
  top: 0;
  position: relative;
  z-index: 1;
  margin-bottom: 25px;
  text-align: center;
  color: white;
}
.panel-info-small {
  padding: 25px;
  background-color: #001825;
}
.panel-info-small > p {
  text-transform: none;
}
.panel-info-small > p:last-child {
  margin-bottom: 0;
}
.panel-info {
  position: relative;
  z-index: 1;
  padding: 35px 60px 45px 75px;
  margin: 0 auto 50px;
  text-align: left;
  max-width: 100%;
  box-sizing: border-box;
  background: radial-gradient(#292c72, #040107) !important;
  border-radius: 12px;
  border: 1px solid #84dcdc;
}
.panel-info:before {
  z-index: -1;
  position: absolute;
  left: 0;
  right: 0;
  top: -32px;
  content: '';
  height: 122px;
}
.panel-info:after {
  z-index: -1;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -8px;
  content: '';
  height: 94px;
}
.panel-info:last-child {
  margin-bottom: 0;
}

.panel-info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 17px;
  margin-bottom: 15px;
  color: white;
}
.panel-info-item a {
  text-transform: none;
  color: #fff;
}
.panel-info-item:last-child {
  margin-bottom: 0;
}
.btn {
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  color: black !important;
  background: #fcc530;
  border-radius: 3px;
}
.panel-info-item .ether-value {
  color: #fff;
}
.panel-info-item > *:first-child {
  text-transform: capitalize;
}

.footer-content {
  padding: 30px 2;
  display: block;
  display: flex;
  height: 150px;
  justify-content: center;
}
.footer-content a {
  display: inline-block !important;
  width: auto;
  text-decoration: none;
  padding: 10px 30px;
  margin: 5px 10px;
}

.copyright {
  font-family: Myriad Pro;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  color: #ffffff;
  text-transform: none;
}

.page {
  max-width: 890px;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
  margin: 0 auto;
  text-transform: none;
  text-align: justify;
  padding: 20px 30px;
  font: 15px/1.4 Roboto, sans-serif;
  box-sizing: border-box;
  background: radial-gradient(#292c72, #040107) !important;
  border-radius: 12px;
  border: 1px solid #84dcdc;
  color: white;
}
.page h3 {
  margin: 0 0 20px;
  color: #fff;
  font-weight: normal;
  font-family: 'Rubik', sans-serif;
  font-size: 30px;
  font-weight: bold;
  text-transform: uppercase;
}
.i-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  overflow: auto;
  z-index: 100;
  padding: 0;
  font: 15px/1.4 Roboto, sans-serif;
}
.i-modal__wrap {
  position: relative;
  cursor: default;
  max-width: 600px;
  margin: 40px auto;
  background: #fff;
  border-radius: 5px;
}
.i-modal__close {
  position: absolute;
  top: 3px;
  right: 3px;
  z-index: 20;
  opacity: 0.7;
  cursor: pointer;
  color: #fff;
  width: 30px;
  height: 30px;
  line-height: 30px;
}
.i-modal__close:hover {
  opacity: 1;
}
.i-modal__body {
  padding: 20px;
  background: url('../../assets/img/bg.jpg') no-repeat center center / cover;
}
.i-modal__title {
  margin-bottom: 20px;
  font-size: 20px;
  color: #fff;
}
.i-modal__text {
  font-size: 16px;
  color: #fff;
}
.i-modal__text a {
  text-decoration: underline;
  color: #fff !important;
}
.header {
  text-align: left;
  text-transform: none;
  font: 15px/1.4 Roboto, sans-serif;
}
.header__wrap {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}
.header__grow {
  flex-grow: 1;
}
.header__item {
  padding: 15px 20px 5px;
}
.header__label {
  color: #fff;
}
.header__value {
}
.lang {
  position: relative;
  cursor: default;
}
.lang div {
  padding: 3px 0 10px;
}
.lang ul {
  padding: 3px 0px;
  margin: 0 0 0 -15px;
  position: absolute;
  top: 100%;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  list-style: none;
  z-index: 999;
  border-radius: 3px;
  display: none;
}
.lang:hover ul {
  display: block;
}
.lang li {
  display: block;
  white-space: nowrap;
}
.lang a {
  display: block;
  padding: 5px 25px 5px 15px;
  cursor: pointer;
  color: #fff;
  text-decoration: none;
}
.lang a:hover {
  background: rgba(0, 0, 0, 0.2);
}
.lang img {
  width: 20px;
  margin: 0 8px 0 0;
  position: relative;
  top: 3px;
}

@media (max-width: 650px) {
  .header__wrap {
    justify-content: center;
    text-align: center;
    display: none;
  }
  .header__grow {
    display: none;
  }
  .header__item:first-child {
    display: none;
  }

  .section-top .top-logo {
    max-height: 30px;
    top: 15px;
  }
}

@media (max-width: 1599px) {
  .section-top {
    padding: 280px 0 45px;
  }
}
@media (max-width: 1199px) {
  .section-top {
    padding: 250px 0 25px;
  }
}
@media (max-width: 959px) {
  .section-top {
    padding: 200px 0 25px;
  }
  .section-top .top-heading {
    display: none;
  }
  .section-top .top-heading-mobile {
    display: block;
    top: 0px;
    width: 37%;
  }

  .footer-content {
    height: 130px;
  }

  .menu {
    display: block;
  }
  .menu > * {
    margin-right: 0;
  }
}
