@media (max-width: 639px) {
  html {
    font-size: 12px;
  }

  h2 {
    font-size: 42px;
    margin: 0 0 25px;
  }

  h3 {
    font-size: 18px;
    margin-bottom: 30px;
  }

  .section-top {
    padding: 120px 0 10px;
  }

  .section {
    padding: 10px 0 10px;
  }

  .container {
    max-width: 100%;
    padding: 0 30px;
  }

  .calc .container {
    padding: 0 30px;
  }

  .panel-join {
    padding: 25px 20px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 10px;
  }

  .panel-join-display {
